.formControl {
  & textarea {
    border: none;
    border-radius: 5px;
    margin: 0 0 10px 0;
    width: 100%;
    height: 50px;
    background-color: #E6EDE9;
  }
  &.error {
    & textarea, input {
      background-color: #ffebe9;
      outline:2px solid #f60303 ;
      &:focus{
        background-color: #fff;
        outline: 2px solid #035cd0;
      }
    }
    & span {
      width: 100%;
      border: none;
      font-size: 10px;
      color: #f60303;
      margin: 10px 0 0 0;
      text-align: right;
      text-transform: none;
    }
  }
}
.field{
  display: flex;
  font-size: 12px;
  & span{
    margin: 0 0 0 10px;
  }
}