.wrapper {
  margin: 30px 0px 20px 20px;
  display: flex;
  align-items: center;
}
.avatar {
  width: 50px;
  height: 50px;
  margin: 0px 20px 0px 0px;

  & img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}
.body {
  font-size: 20px;
  color: blue;
}
.likes {
  font-weight: 700;
  margin: 0px 0px 0px 30px;
  & > span {
    font-size: 14px;
    color: red;
    margin: 0px 0px 0px 10px;
    &:first-child {
      font-size: 12px;
      font-weight: normal;
      color: #000;
    }
  }
}
