.wrapper {
  width: 100%;
}
.user {
  font-size: 16px;
  display: flex;
  padding: 30px 10px 10px 10px;

}
.photo {
  width: 100px;
  height: 100px;
  margin: 0 40px 50px 0;
  & img {
    width: 100%;
    margin: 0 0 5px 0;
  }
  & input{
    font-size: 10px;
  }& button{
    font-size: 10px;
  margin: 5px 0 20px 0;
  }
}
.description {
  width: 100%;
}
.name {
  font-size: 22px;
  font-weight: bold;
  color: #05b61d;
  margin: 0 0 10px 0;
}
.aboutMe {
  font-weight: bold;
  color: #6a113f;
  & > span {
    font-weight: normal;
    color: #000;
  }
}

