.wrapper {
  padding: 10px;
  margin: 20px 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.posts {
  margin: 10px;
}
.post_new{
  width: 95%;
  margin: 20px 0 0 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  &>textarea{
    border: none;
    margin: 0 0 10px 0;
    width: 100%;
    height: 50px;
    background-color: #E6EDE9;
  }
  &>button{
    background-color: #00A157;
    border:none;
    box-shadow: none;
    color: #fff;
    padding: 2px 40px 2px 5px;
  }

}